.App {
  width: 100%;
  height: 100vh;
}

.container {
  position: relative;
  width: 100%;
  padding: 50px 30px;
}

.error {
  position: absolute;
  top: 20%;
  left: 50%;
  z-index: 30;
  transform: translate(-50%, -100%);
  color: tomato;
  text-shadow: 0px 2px 12px rgb(0 0 0 / 14%);
}
